<template>
	<div id="app">
	    <div class="header">
			<div id="header-text"></div>
			<div id="header-link"><a href="javascript:void(0)" target="_blank">小牛在线管理平台</a>&nbsp;&nbsp;|&nbsp;&nbsp;<a href="javascript:void(0)" target="_blank">帮助与文档</a></div>
		</div>
		<div class="login">
			<table width="980px" border="0" align="center" class="layout">
				<tr>
					<td align="left" width="680px">
						<div id="banner">
						    <div id="banner_bg"></div>
							<!--标题背景-->
						    <div id="banner_info"></div>
							<!--标题-->

						    <div id="banner_list">

						    </div>
						</div>
					</td>
					<td align="center" width="300px">
						<div class="user-login">
						<div class="user-login-title">小牛在线管理平台  用户登录</div>
						<div>
							<form action="" id="loginForm">
								<ul class="login-form">
									<li class="user">
										<div class="ico "></div>
										<input type="text" class="ipt wid1 ipt-focus" placeholder="账号" v-model="userId" name="userId" /></li>
									<li class="password">
										<div class="ico "></div>
										<input type="text" class="ipt wid1 ipt-focus" placeholder="密码" onfocus="this.type='password'" @keyup.enter="login"  v-model="password" name="passowrd" /></li>
									<li class="button-row">
										<input type="button" class="btn btn1" value="登 录" id="login" v-on:click="login" />
									</li>
								</ul>
							</form>
						</div>
						<div id="msg"></div>
						</div>
					</td>
				</tr>
			</table>
		</div>
	</div>
</template>

<script>
import auth from 'components/api/auth'
import axios from 'axios'
export default {
  data () {
    return {
      userId: '',
      password: ''
    }
  },

  methods: {
    login(){
        console.info(loginParams);
    	var loginParams = { userId: this.userId, password: this.password,
             type: '1' };
        auth.login(loginParams).then(resp => {
          this.logining = false;
          let { msg, code, data } = resp;
          if (code !== '00000000') {
            this.$notify({
              title: '错误',
              message: msg,
              type: 'error',
							duration: 1000
            });
          } else {
          	axios.defaults.headers.common['accessToken'] = data.token;
            sessionStorage.setItem('token', data.token);
            sessionStorage.setItem('user', JSON.stringify(data.user));
            window.location.href = "./";
          }
        });
    }
  }
}
</script>

<style>
body, div, dl, dt, dd, ul, ol, li, h1, h2, h3, h4, h5, h6, pre, code,
	form, fieldset, legend, input, button, textarea, p, blockquote, th, td
	{
	margin: 0px;
	padding: 0px;
	font-family: "\5FAE\8F6F\96C5\9ED1";
}

/* login page header */
div.header {
	height: 60px;
	font-size: 22px;
	line-height: 60px;
	padding-left: 166px;
	color: #333;
	font-family: "\5FAE\8F6F\96C5\9ED1";
	background-repeat: no-repeat;
	background-position: 1.2% 10px;
	background-size: 110px;
	background-color: #fafafa;
	border-bottom: 1px solid #f0f0f0;
}

#header-text {
	color: #444;
	font-weight: bold;
}

#header-link {
	position: absolute;
	right: 1.2%;
	top: 0px;
	font-size: 14px;
}

a:link {
	color: #444;
	text-decoration: none;
}

a:visited {
	color: #444;
	text-decoration: none;
}

a:hover {
	color: orange;
	text-decoration: none;
}

a:active {
	color: #444;
	text-decoration: none;
}

/* login page body */
div.login {
	width: 100%;
	height: 60%;
	position: absolute;
	top: 20%;
	text-align: center;
	padding: 0;
	margin: 0;
}

#msg {
	height: 20px;
	margin: 10px;
}

ul.login-form {
	width: 240px;
	padding-top: 10px;
	position: relative;
	list-style: none;
}

ul.login-form li.user, .login-form li.password {
	position: relative;
	border: 1px solid #c8c8c8;
	padding: 0 0 0 41px;
	height: 40px;
	background: #fff;
	font-size: 14px;
	font-weight: bold;
	margin-top: -1px;
}

ul.login-form li.user {
	border-radius: 3px 3px 0 0;
	-moz-border-radius: 3px 3px 0 0;
	-webkit-border-radius: 3px 3px 0 0;
}

ul.login-form li.password {
	border-radius: 0 0 3px 3px;
	-moz-border-radius: 0 0 3px 3px;
	-webkit-border-radius: 0 0 3px 3px;
}

ul.login-form li .ico {
	position: absolute;
	left: 0px;
	top: 0px;
	background-color: #eee;
	width: 40px;
	height: 40px;
}

ul.login-form li.user .ico {
	border-radius: 3px 0 0 0;
	-moz-border-radius: 3px 0 0 0;
	-webkit-border-radius: 3px 0 0 0;
}

ul.login-form li.password .ico {
	border-radius: 0 0 0 3px;
	-moz-border-radius: 0 0 0 3px;
	-webkit-border-radius: 0 0 0 3px;
}

table.layout {
	border: 2px solid #323b40;
	background: #323b40;
	border-radius: 8px;
	-moz-border-radius: 8px;
	-webkit-border-radius: 8px;
}

div.user-login-title {
	color: #fff;
	font-size: 18px;
	padding-bottom: 20px;
}

table.layout div.user-login {
	border: 0px solid #999999;
	border-radius: 3px;
	-moz-border-radius: 3px;
	-webkit-border-radius: 3px;
	width: 260px;
	padding-top: 20px;
	padding-bottom: 0px;
}

.ico {
	display: inline-block;
}

.ico-mail {
	background: url('/assets/img/icon_user.png') no-repeat center center;
	width: 18px;
	height: 18px;
}

.ico-pwd {
	background: url('/assets/img/icon_pwd.png') no-repeat center center;
	width: 18px;
	height: 18px;
}

.ipt {
	fon-size: 14px;
	background: #fff;
	padding: 15px 0;
	height: 18px;
	line-height: 18px;
	color: #999;
	border: none;
}

.ipt-focus {
	color: #000;
}

.login-form .wid1 {
	width: 96%;
	padding: 11px 2%;
	border: 0px solid;
}

input.btn {
	border: 0 none;
}

.btn {
	display: inline-block;
	cursor: pointer;
	border-radius: 3px;
	-moz-border-radius: 3px;
	-webkit-border-radius: 3px;
}

.btn1 {
	background: #0099CB;
	color: #fff;
	width: 100%;
	height: 40px;
	line-height: 40px;
	font-size: 16px;
}

.btn1:hover {
	background: #00a3d2;
}

.error {
	vertical-align: middle;
	color: red;
	font-size: 14px;
}

.button-row {
	margin-top: 20px;
}

/* login page footer */
div.footer {
	color: #33333;
	font-size: 12px;
	text-align: center;
	padding: 10px;
	width: 100%;
	margin: 0;
	position: fixed;
	left: 0;
	bottom: 0px;
	/* background: #fafafa;
	border-top: 1px solid #f0f0f0; */
}

/* banner */
#banner {
	position: relative;
	width: 680px;
	height: 400px;
	border-right: 1px solid #999999;
	/* overflow: hidden;
	border-radius: 44px;
	-moz-border-radius: 44px;
	-webkit-border-radius: 44px;
	background: #444; */
}

#banner_list img {
	border: 0px;
}

#banner_bg {
	position: absolute;
	bottom: 0;
	background-color: ;
	height: 30px;
	filter: Alpha(Opacity = 50);
	opacity: 0.5;
	z-index: 1000;
	cursor: pointer;
	width: 800px;
}

#banner_info {
	position: absolute;
	bottom: 10px;
	left: 80px;
	height: 22px;
	color: #0AE;
	font-weight: ;
	cursor: pointer;
	z-index: 1001;
}

#banner_text {
	position: absolute;
	width: 120px;
	z-index: 1002;
	right: 3px;
	bottom: 3px;
}

#banner ul {
	position: absolute;
	list-style-type: none;
	/* filter: Alpha(Opacity = 80);
	opacity: 0.8; */
	z-index: 1002;
	margin: 0;
	padding: 0;
	bottom: 10px;
	right: 80px;
}

#banner ul li {
	font-size: 14px;
	padding: 0px 6px;
	float: left;
	display: block;
	color: #FFF;
	background: #0AE;
	cursor: pointer;
	border: 0px solid #fff;
	margin-left:2px;
	border-radius: 16px;
	-moz-border-radius: 16px;
	-webkit-border-radius: 16px;
}

#banner ul li.on {
	background-color: #fff;
	color: #444;
}

#banner_list a {
	position: absolute;
}
</style>



